import tools from '../../lib/tools.js';
export default {
    name:'sharing',
    data(){
        return {
            id:'',
            type:1,
            sourceUserId:'',
            info:{},
        }
    },
    created(){
        let params = tools.getRequestParam();
        this.type = params.type;
        this.id = params.id;
        this.sourceUserId = params.sourceUserId || '';
    },
    mounted(){
        this.getService();
    },
    methods:{
        getService(){
            let vm = this;
            if(!(this.id&&this.type)){alert('参数错误');return false;}
            this.$http.post('/qrcode/getShareServiceCode?paramsId='+this.id+'&type='+this.type+'&sourceUserId='+this.sourceUserId,{}).then((result) => {
                let data = result.data;
                if(data.successful && (data.status==200)){
                    if(data.data){
                        vm.info = data.data;
                        if(vm.type==4){
                            document.getElementById('main').innerHTML = data.data.desc;
                            const imgs = document.querySelectorAll('#main img');
                            for(var i=0;i<imgs.length;i++){
                                imgs[i].className = 'img-responsive';
                                imgs[i].style.display = 'block';
                                imgs[i].style.maxWidth = '100%';
                                imgs[i].style.height = 'auto';
                            }
                        }
                    }
                }else{
                    alert('查询失败');
                    console.error(data);
                }
            },(error) => {
                alert('查询失败');
                console.error(error);
            });
        }
    }
}



